
import type { Metadata } from 'next';
// Import actual Google Fonts: Inter for sans-serif and Roboto_Mono for monospace
import { Inter, Roboto_Mono } from 'next/font/google';
import './globals.css';
import { Toaster } from "@/components/ui/toaster";
import AppHeader from '@/components/reflectify/AppHeader';

// Instantiate Inter font
const inter = Inter({
  variable: '--font-geist-sans', // Keep existing CSS variable name for compatibility with globals.css
  subsets: ['latin'],
  display: 'swap', // Improve font loading performance and user experience
});

// Instantiate Roboto_Mono font
const roboto_mono = Roboto_Mono({
  variable: '--font-geist-mono', // Keep existing CSS variable name
  subsets: ['latin'],
  display: 'swap', // Improve font loading performance and user experience
});

export const metadata: Metadata = {
  title: 'Quick Goals',
  description: 'Process your thoughts and track your mental well-being.',
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en">
      {/* Update className to use the new font instance variables */}
      <body className={`${inter.variable} ${roboto_mono.variable} antialiased bg-background text-foreground`}>
        <div className="flex flex-col min-h-screen">
          <AppHeader />
          <main className="flex-grow">
            {children}
          </main>
        </div>
        <Toaster />
      </body>
    </html>
  );
}
